.newsletter {
  overflow: visible;
  height: 40px;
  width: 300px;
  margin: 0 auto;
  position: relative;
}
.newsletter input {
  width: 100%;
  border: 1px solid #007f00;
  border-radius: 5px;
  background-color: beige;
  height: 40px;
  padding: 10px 0 10px 15px;
  outline: none;
  -moz-transition: border 0.2s ease;
  -o-transition: border 0.2s ease;
  -webkit-transition: border 0.2s ease;
  transition: border 0.2s ease;
}
.newsletter input:focus {
  color: #007f00;
}
.newsletter button {
  position: absolute;
  z-index: 999;
  right: 2px;
  top: 2px;
  height: 56px;
  border: none;
  background-color: beige;
  border-left: 1px solid #007f00;
  -moz-transition: background-color 0.3s ease, width 0.3s ease;
  -o-transition: background-color 0.3s ease, width 0.3s ease;
  -webkit-transition: background-color 0.3s ease, width 0.3s ease;
  transition: background-color 0.3s ease, width 0.3s ease;
  color: #007f00;
  padding: 0;
  margin: 0;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  width: 135px;
  text-align: center;
  cursor: pointer;
  outline: none;
}

.sp {
  margin-bottom: 50px;
}

.sp small {
  color: #007f00;
  font-size: 11px;
}

.link {
  color: #007f00;
}
.link:hover {
  cursor: pointer;
  border-bottom: 3px solid #007f00;
}

.cardLink {
  color: beige;
}

.cardLink:hover {
  cursor: pointer;
  border-bottom: 3px solid beige;
}


.anx {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 10px;
  padding-bottom: 10px;
  
}

.sym h1 {
  text-align: center;
}

.sym h3 {
  text-align: center;
  border-bottom: 1px solid #e8e8e8;
  margin-top: 40px;
}

.builtby {
  color: #007f00 !important;
  display: inline-block;
  margin-top: 100px;
  font-size: 90%;
}

.builtby {
  position: relative;
  padding: 10px 0;
  border-top: 2px solid hsla(0,0%,100%,0.1);
  color: hsla(0,0%,100%,0.7);
  font-family: 'Inter', sans-serif;
  font-weight: 450;
  font-size: 15px;
  text-align: center;
}



